import React from 'react';
import {Link} from 'react-router-dom';
import Anime from "react-anime";

import './ActividadesMain.scss';

const ActividadesMain = () => {
    return ( 
        <div className="actividades-main">
            <div className="goback"><i className="fas fa-arrow-left"></i><Link to="/">Volver</Link></div>
            <Anime delay={(e, i) => i * 500} opacity={[0, 1]} scale={[1.2, 1]}>
                <div className="card pictogramas"><h2>Pictogramas</h2></div>
                <div className="card memotests"><h2>Memotests</h2></div>
            </Anime>
        </div>
    );
}
 
export default ActividadesMain;