import React from 'react';
import {Helmet} from 'react-helmet';

// Components //
import ActividadesMain from '../components/common/Actividades/ActividadesMain/Actividades';

const Home = () => {
    return ( 
        <>
        <Helmet>
            <title>PIPND - Actividades</title>
            <meta name="description" content="PIPND - Pagina Interactiva para Niños con Discapacidad" data-react-helmet="true"/>
        </Helmet>
        <div>
        <ActividadesMain />
        </div>
        </>
    );
}
 
export default Home;