import React from "react";
import Anime from "react-anime";
import {Link} from 'react-router-dom';

import inclusion from '../../../assets/inclusion_ec.jpg';

import "./MainScreen.scss";

const MainScreen = () => {
  const onClick = () => {
    document
      .querySelector(".about-pipnd")
      .classList.toggle("about-pipnd-change");
  };

  return (
    <div className="main-screen">
      <div className="title">
        <Anime
          delay={(e, i) => i * 200}
          scale={[0, 1]}
          translateY={["-20em", "0em"]}
          opacity={[0, 1]}
        >
          <h2>P</h2>
          <h2>I</h2>
          <h2>P</h2>
          <h2>N</h2>
          <h2>D</h2>
        </Anime>
      </div>

      <Anime delay={500} scale={[0, 1]}>
        <h1>Bienvenido!</h1>
      </Anime>
      <div className="nav">
        <Anime opacity={[0, 1]} delay={500} scale={[0.5, 1]}>
          <Link to="/actividades">Actividades</Link>
          <Link to="/recursos">Recursos</Link>
          <Link to="/admin">Administrador</Link>
          <a onClick={onClick}>¿Qué es PIPND?</a>
        </Anime>
      </div>

      <div className="about-pipnd">
        <div className="about-pipnd__content">
          <button className="btn-close" onClick={onClick}>Cerrar</button>
          <div className="text">
            <h1>PIPND (Página Interactiva Para Niños con Discapacidad)</h1>
            <p>
              PIPND es un proyecto destinado a recolectar actividades y recursos
              visuales para ayudar y asistir a niños con discapacidad, como por
              ejemplo, autismo. La idea del proyecto es brindar una herramienta
              a psicopedagogos/as y fonoudiólogos/as para poder tener a mano
              actividades, recursos, blogs, textos, y cualquier material que les
              sirva para desempeñar su trabajo. Cualquiera puede aportar a este
              proyecto, ya que es Open Source sin fines de lucro.
            </p>
            <p>Para saber más sobre cómo ayudar en el proyecto, ir a la página de <a href="https://www.github.com" target="_blank">GitHub</a></p>
          </div>
          <img src={inclusion} alt="inclusion-no discriminar" className="inclusion-img"/>
        </div>
      </div>
    </div>
  );
};

export default MainScreen;
