import React from "react";
import {Link} from 'react-router-dom'

import "./Recursos.scss";

import tuto1 from "../../../assets/tuto1.JPG";
import tuto2 from "../../../assets/tuto2.JPG";

const Recursos = () => {
  return (
    <div className="recursos-wp">
        <div className="goback"><i className="fas fa-arrow-left"></i><Link to="/">Volver</Link></div>
      <h1>Recursos</h1>
      <p>
        A continuación encuentra un simple tutorial de cómo descargar
        los recursos desde el repositorio de GitHub
      </p>
      <div className="tutorial">
        <div className="tut">
            <p>- Ir al repositorio de github : <a href="https://github.com/AleYanc/recursos-pipnd" target="_blank">Click aqui</a></p>
            <p>- Dar click en el archivo que desea descargar</p>
          <img src={tuto1} alt="" />
        </div>

        <div className="tut">
            <p>- Click en el botón "Download" o "Descargar"</p>
          <img src={tuto2} alt="" />
        </div>
      </div>
    </div>
  );
};

export default Recursos;
