import React from 'react';
import {Helmet} from 'react-helmet';

// Components //
import MainScreen from '../components/common/MainScreen';

const Home = () => {
    return ( 
        <>
        <Helmet>
            <title>PIPND</title>
            <meta name="description" content="PIPND - Pagina Interactiva para Niños con Discapacidad" data-react-helmet="true"/>
        </Helmet>
        <div>
        <MainScreen />
        </div>
        </>
    );
}
 
export default Home;